.map{
 height: 500px;
 background-color: #fff;
 padding: 1rem;
 border-radius: 20px;
 margin-top: 16px;
 box-shadow: 0 0 8px -4px rgba(0,0,0,0.5);
}

.map .leaflet-container{
 height: 100%;
}

.info-flag{
 width: 100%;
 height: 80px;
 background-size: cover;
 border-radius: 2px;
 margin: auto;
}

.info-flag img{
 width : 100px;
 height: 10px;
 border-radius: 5px;
}

.info-name{
 font-size: 20px;
 font-weight: bold;
 color: #555;
}

.info-confirmed,
.info-recovered,
.info-deaths{
 font-size: 16px;
 margin-top: 5px;
}